.hero-carousel {
  //.slick-dots {
  //  top: 0;
  //  bottom: auto;
  //  right: 0;
  //  left: auto;
  //  width: auto;
  //}
  .carousel__item {
    font-size: 0.5em;
    max-height: 40rem;

    //picture {
    //  background-position: center center;
    //  background-size: cover;
    //  padding-top: 50%;
    //}

    .carousel__item-text__wrap {
      color: #fff;
      position: relative;
      width: 100%;
      z-index: 100;
      background-color: #210b00;

      @media (min-width: 650px) {
        position: absolute;
        left: 0;
        bottom: 0;
        background-color: transparent;
        background-image: linear-gradient(to bottom, rgba(0, 41, 56, 0), rgba(0, 41, 56, 0.9));
      }

      @media (min-width: 900px) {
        padding: 7rem calc(100vw / 7) 0;
      }

      @media (min-width: 1800px) {
        height: 100%;
        padding: 0;
        width: calc(100vw / 7);
      }

      h2 {
        font-weight: 800;
      }

      .l-container {
        padding: 1rem;

        @media (min-width: 1800px) {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
        }

        p {
          display: none;

          @media (min-width: 900px) {
            display: block;
          }
        }
      }
    }
  }
}

